import React, { Component } from 'react';
import { UncontrolledDropdown, DropdownItem, DropdownMenu, DropdownToggle, Nav } from 'reactstrap';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import * as action from '../../redux/actions/authAction'
import { FormCheckbox } from "shards-react";
import { AppNavbarBrand, AppSidebarToggler } from '@coreui/react';
// import logo from '../../assets/img/brand/logo.svg'
// import sygnet from '../../assets/img/brand/sygnet.svg'

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};

class DefaultHeader extends Component {
  render() {

    // eslint-disable-next-line
    const { children, ...attributes } = this.props;

    return (
      <React.Fragment>
        <AppSidebarToggler className="d-lg-none" display="md" mobile />
        <AppNavbarBrand
          // full={{ src: logo, width: 150, height: 150, alt: 'OMRON Logo' }}
          // minimized={{ src: logo, width: 100, height: 100, alt: 'OMRON Logo' }}
        />
        <AppSidebarToggler className="d-md-down-none" display="lg" />
        <Nav className="d-md-down-none" navbar>
          <div style={{ marginLeft: 50, padding: 0, fontSize: 25, fontWeight: 'bold' }}>{this.props.namePage}</div>
        </Nav>
        {/* <Nav className="d-md-down-none" navbar>
          <NavItem className="px-3">
            <NavLink to="/scada/mixing" className="nav-link" >Mixing</NavLink>
          </NavItem>
          <NavItem className="px-3">
            <Link to="/scada/mobilerobot" className="nav-link">Mobile Robot</Link>
          </NavItem>
          <NavItem className="px-3">
            <NavLink to="/scada/robot" className="nav-link">Robot</NavLink>
          </NavItem>
        </Nav> */}
        <Nav className="ml-auto" navbar>
          {/* <NavItem className="d-md-down-none">
            <NavLink to="#" className="nav-link"><i className="icon-bell"></i><Badge pill color="danger">5</Badge></NavLink>
          </NavItem>
          <NavItem className="d-md-down-none">
            <NavLink to="#" className="nav-link"><i className="icon-list"></i></NavLink>
          </NavItem>
          <NavItem className="d-md-down-none">
            <NavLink to="#" className="nav-link"><i className="icon-location-pin"></i></NavLink>
          </NavItem> */}
          <UncontrolledDropdown nav direction="down">
            <DropdownToggle nav>
              <img src={require('../../images/img_design.png')} className="img-avatar" alt="Design Scada" />
            </DropdownToggle>
            {this.props.idUser === '1' ?
              <DropdownMenu right>
                <DropdownItem header tag="div" className="text-center" ><strong>{this.props.emailUser}</strong></DropdownItem>
                <DropdownItem header tag="div" className="text-center" ><strong>Design Scada</strong></DropdownItem>
              </DropdownMenu>
              :
              <DropdownMenu>
                <DropdownItem header tag="div" className="text-center" ><strong>{this.props.emailUser}</strong></DropdownItem>
                <DropdownItem header tag="div" className="text-center"><strong>LOGOUT</strong></DropdownItem>
                <DropdownItem>
                  {
                    <FormCheckbox toggle small inline
                      checked={false}
                      onChange={() => this.props.actionLogout()}>
                      LogOut
                    </FormCheckbox>
                  }
                </DropdownItem>
              </DropdownMenu>
            }
          </UncontrolledDropdown>
        </Nav>
        {/* <AppAsideToggler className="d-md-down-none" /> */}
        {/*<AppAsideToggler className="d-lg-none" mobile />*/}
      </React.Fragment>
    );
  }
}

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;
const mapStateToProps = (state, ownProps) => {
  return {
    timeServer:state.navPageFull.timeServer,
    emailUser: state.firebase.auth.email,
    nameUser: state.auth.displayName,
    idUser: state.auth.idAdmin,
    checkedAddImage: state.auth.addImage,
    checkedEditImage: state.auth.editImage,
    checkedAddLabel: state.auth.addLabel,
    checkedEditLabel: state.auth.editLabel,
    checkedAddLine: state.auth.addLine,
    checkedEditLine: state.auth.editLine,
    checkedEditWordDisplay: state.auth.editWordDisplay,
    checkedEditWordAdjust: state.auth.editWordAdjust,
    checkedEditDWordDisplay: state.auth.editDWordDisplay,
    checkedEditDWordAdjust: state.auth.editDWordAdjust,
    checkedEditBitDisplay: state.auth.editBitDisplay,
    checkedEditBitAdjust: state.auth.editBitAdjust,
    checkedEditPanel: state.auth.editPanel,

    checkedAddWordDisplay: state.auth.addWordDisplay,
    checkedAddWordAdjust: state.auth.addWordAdjust,
    checkedAddDWordDisplay: state.auth.addDWordDisplay,
    checkedAddDWordAdjust: state.auth.addDWordAdjust,
    checkedAddBitDisplay: state.auth.addBitDisplay,
    checkedAddBitAdjust: state.auth.addBitAdjust,
    checkedAddPanel: state.auth.addPanel,

    checkedEditTextField: state.auth.editTextField,
    checkedAddTextField: state.auth.addTextField,
    checkedEditTextFieldInput: state.auth.editTextFieldInput,
    checkedAddTextFieldInput: state.auth.addTextFieldInput,

    checkedEditAll: state.auth.edit,
    namePage: state.navPageFull.namePage
  }
}


const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionToggleAddImage: () => {
      dispatch(action.actionToggleAddImage())
    },
    toggleEditImage: () => {
      dispatch(action.toggleEditImage())
    },
    actionToggleAddLabel: () => {
      dispatch(action.actionToggleAddLabel())
    },
    toggleEditLabel: () => {
      dispatch(action.toggleEditLabel())
    },
    actionToggleAddLine: () => {
      dispatch(action.actionToggleAddLine())
    },
    actionToggleEditLine: () => {
      dispatch(action.actionToggleEditLine())
    },
    actionToggleEditWordDisplay: () => {
      dispatch(action.actionToggleEditWordDisplay())
    },
    actionToggleEditWordAdjust: () => {
      dispatch(action.actionToggleEditWordAdjust())
    },
    actionToggleEditDWordDisplay: () => {
      dispatch(action.actionToggleEditDWordDisplay())
    },
    actionToggleEditDWordAdjust: () => {
      dispatch(action.actionToggleEditDWordAdjust())
    },
    actionToggleEditBitDisplay: () => {
      dispatch(action.actionToggleEditBitDisplay())
    },
    actionToggleEditBitAdjust: () => {
      dispatch(action.actionToggleEditBitAdjust())
    },
    actionToggleEditPanel: () => {
      dispatch(action.actionToggleEditPanel())
    },

    actionToggleAddWordDisplay: () => {
      dispatch(action.actionToggleAddWordDisplay())
    },
    actionToggleAddWordAdjust: () => {
      dispatch(action.actionToggleAddWordAdjust())
    },
    actionToggleAddDWordDisplay: () => {
      dispatch(action.actionToggleAddDWordDisplay())
    },
    actionToggleAddDWordAdjust: () => {
      dispatch(action.actionToggleAddDWordAdjust())
    },
    actionToggleAddBitDisplay: () => {
      dispatch(action.actionToggleAddBitDisplay())
    },
    actionToggleAddBitAdjust: () => {
      dispatch(action.actionToggleAddBitAdjust())
    },
    actionToggleAddPanel: () => {
      dispatch(action.actionToggleAddPanel())
    },

    actionToggleAddTextField: () => {
      dispatch(action.actionToggleAddTextField())
    },
    actionToggleEditTextField: () => {
      dispatch(action.actionToggleEditTextField())
    },
    actionToggleAddTextFieldInput: () => {
      dispatch(action.actionToggleAddTextFieldInput())
    },
    actionToggleEditTextFieldInput: () => {
      dispatch(action.actionToggleEditTextFieldInput())
    },

    actionToggleOffAll: () => {
      dispatch(action.actionToggleOffAll())
    },
    actionLogout: () => {
      dispatch(action.signOut())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(DefaultHeader);
